<template>
  <div class="listView">
    <div class="listBtn">
      <el-switch
        v-model="status"
        on-text=""
        off-text=""
        @change="toggleDinner">
      </el-switch>
      <span>{{status ? '点餐开启' : '点餐关闭'}}</span>
    </div>

      <el-table
        :data="tableData"
        stripe
        width="100%">
        <el-table-column
          type="index"
          width="100"
          align="center">
        </el-table-column>
        <el-table-column
          prop="userName"
          label="姓名"
          align="center">
        </el-table-column>
        <el-table-column
          prop="type"
          label="主食"
          align="center">
        </el-table-column>
        <el-table-column
          prop="count"
          label="数量"
          align="center">
        </el-table-column>
        <el-table-column
          prop="createdAt"
          label="时间"
          width="180"
          align="center">
        </el-table-column>
        <el-table-column
          prop="others"
          label="备注"
          align="center">
        </el-table-column>
        <el-table-column
          label="操作"
          width="120"
          align="center">
          <template scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index,scope.row.objectId)"
              type="text"
              size="small">
              撤销
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="tableSum">
        <P>米饭：<span>{{rice}}</span></P>
        <P>馒头：<span>{{buns}}</span></P>
      </div>
  </div>
</template>

<script>
import Bus from '../bus'

export default {
  name: 'listView',
  data () {
    return {
      status: true,
      tableData: [],
      rice: 0,
      buns: 0
    }
  },
  created: function () {
    getAllData.bind(this)()
    function getAllData () {
      const url = 'getAllDinner'
      Bus.get(url, (data) => {
        if (data.code === 1) {
          this.status = data.result.dinnerStatus
          let tableDataArr = data.result.results
          for (let n in tableDataArr) {
            // 份数
            if (tableDataArr[n].count === 1) {
              tableDataArr[n].count = '0.5'
            } else if (tableDataArr[n].count === 2) {
              tableDataArr[n].count = '1'
            } else if (tableDataArr[n].count === 3) {
              tableDataArr[n].count = '2'
            } else {
              tableDataArr[n].count = '3'
            }
            // 主食
            if (tableDataArr[n].type === 1) {
              tableDataArr[n].type = '米饭'
              this.rice = parseFloat(tableDataArr[n].count) + this.rice
            } else {
              tableDataArr[n].type = '馒头'
              this.buns = parseFloat(tableDataArr[n].count) + this.buns
            }
          }
          this.tableData = tableDataArr
        } else {
          this.$message({
            type: 'error',
            message: data.msg
          })
        }
      })
    }
  },
  methods: {
    // 开启、关闭点单状态
    toggleDinner () {
      Bus.get('toggleDinner', (data) => {
        this.$message({
          type: 'success',
          message: data.msg
        })
      })
    },

    // 删除行
    deleteRow (index, objectId) {
      const url = `cancelDinner/${objectId}`
      Bus.get(url, (data) => {
        if (data.code === 1) {
          this.$message({
            type: 'success',
            message: data.msg
          })
          this.tableData.splice(index, 1)
        } else {
          this.$message({
            type: 'error',
            message: data.msg
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

</style>



