<template>
  <div id="order" class="main">
    <div class="orderItem"  v-show="btnShow">
      <span v-html="itemMsg"></span>
      <button @click="backout">撤销</button>
    </div>
    <div class="btnOut">
      <div class="slideOut">
        <transition name="slide-fade">
          <div class="orderForm">
              <p>姓名</p>
              <p>主食</p>
              <p>份数</p>
              <!-- <input type="text" value="" placeholder="输入姓名"/> -->
              <el-form ref="form" :model="form" :rules="rules">
                <el-form-item prop="userName">
                  <el-input
                    size="small"
                    placeholder="输入姓名"
                    v-model="form.userName">
                  </el-input>
                </el-form-item>
                  <!-- <select>
                    <option>米饭</option>
                    <option>馒头</option>
                  </select> -->
                  <el-form-item prop="foodType">
                    <el-select size="small" v-model="form.foodType" placeholder="请选择">
                      <el-option
                        v-for="item in typeOption"
                        :key="item.value"
                        :label="item.label"
                        :value="item">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <!-- <select>
                    <option>0.5</option>
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                  </select> -->
                  <el-form-item prop="count">
                  <el-select size="small" v-model="form.count" placeholder="请选择">
                    <el-option
                      v-for="item in countOption"
                      :key="item.value"
                      :label="item.label"
                      :value="item">
                    </el-option>
                  </el-select>
                  </el-form-item>
                  <!-- <textarea value="" placeholder="备注"></textarea> -->
                  <el-input
                    type="textarea"
                    placeholder="备注"
                    v-model="form.others">
                  </el-input>
              </el-form>
          </div>
        </transition>
<!--         <i class="toRight" :class="arrowClass" @click="orderShow=!orderShow"></i> -->
        <button class="orderBtn" @click="onSubmit" :style="styleClass">
          {{submitText}}
        </button>
      </div>

    </div>
  </div>
</template>

<script>
  import Bus from '../bus'
  import utils from '../utils' // 日期格式化

  // if (localStorage.addDataStr) {
  //   sd()
  // }
  // function sd () {
  //   alert('有')
  // }
  export default {
    name: 'order',
    data () {
      return {
        submitText: '我要点餐',
        styleClass: '',
        btnShow: false,
        // orderShow: true,
        arrowClass: 'el-icon-d-arrow-right',
        typeOption: [{
          value: 1, label: '米饭'
        }, {
          value: 2, label: '馒头'
        }],
        countOption: [{
          value: 1, label: '0.5'
        }, {
          value: 2, label: '1'
        }, {
          value: 3, label: '2'
        }, {
          value: 4, label: '3'
        }],
        form: {
          userName: '',
          foodType: '',
          count: '',
          others: ''
        },
        rules: {
          userName: [
            { required: true, message: '请输入姓名', trigger: 'blur' }
          ],
          foodType: [
            { required: true, message: '请选择主食', trigger: 'change', type: 'object' }
          ],
          count: [
            { required: true, message: '请选择数量', trigger: 'change', type: 'object' }
          ]
        },
        itemMsg: '',
        addDataStr: '', // 本地缓存
        objectId: ''
        // disabled: false
      }
    },
    created: function () {
      if (localStorage.addDataStr) {
        getAddData.bind(this)()
      }
      function getAddData () {
        let addDataObj = JSON.parse(localStorage.addDataStr)
        // let viewDataObj = JSON.parse(localStorage.viewDataStr)
        this.form.userName = addDataObj.userName
        // 主食
        if (addDataObj.type === 1) {
          this.form.foodType = this.typeOption[0]
        } else {
          this.form.foodType = this.typeOption[1]
        }
        // 份数
        if (addDataObj.count === 1) {
          this.form.count = this.countOption[0]
        } else if (addDataObj.count === 2) {
          this.form.count = this.countOption[1]
        } else if (addDataObj.count === 3) {
          this.form.count = this.countOption[2]
        } else {
          this.form.count = this.countOption[3]
        }
        // this.itemMsg = '<em>' + viewDataObj.myTime + '</em>' + '<em>' + addDataObj.userName + '</em>' + '<em>' + viewDataObj.type + '</em>' + '(' + viewDataObj.count + ')'
        // this.btnShow = true

        // 查询
        const url = `getDinner/${addDataObj.userName}`
        Bus.get(url, (data) => {
          if (data.code === 1) {
            // this.$message({
            //   type: 'success',
            //   message: data.msg
            // })
            let lastData = data.result[0]
            if (lastData) {
              let foodCount
              let food
              // 份数
              if (lastData.count === 1) {
                foodCount = '0.5'
              } else if (lastData.count === 2) {
                foodCount = '1'
              } else if (lastData.count === 3) {
                foodCount = '2'
              } else {
                foodCount = '3'
              }
              // 主食
              if (lastData.type === 1) {
                food = '米饭'
              } else {
                food = '馒头'
              }
              this.itemMsg = '<em>' + lastData.createdAt + '</em>' + '<em>' + lastData.userName + '</em>' + '<em>' + food + '</em>' + '(' + foodCount + ')'
              this.btnShow = true
              this.styleClass = 'background:#efa867'
              this.submitText = '已点餐'
              // this.disabled = true
              // 取得objectId
              this.objectId = lastData.objectId
            }
          } else {
            this.$message({
              type: 'error',
              message: data.msg
            })
          }
        })
      }
    },
    // watch: {
    //   orderShow: function () {
    //     if (this.orderShow) {
    //       this.arrowClass = 'el-icon-d-arrow-right'
    //     } else {
    //       this.arrowClass = 'el-icon-d-arrow-left'
    //     }
    //   }
    // },
    methods: {
      onSubmit () { // 提交
        let myDate = new Date()
        // let myHours = myDate.getHours().toString()
        // let myMin = myDate.getMinutes().toString()
        // if (myMin.length < 2) {
        //   myMin + '0'
        // }
        // let myTime = myHours + ':' + myMin
        let myTime = utils.formatDate(myDate, 'datetime')
        if (this.btnShow !== false) {
          this.$message({
            type: 'error',
            message: '您已点餐，如需重点，请先撤销！'
          })
        }
        // 表单验证
        this.$refs.form.validate((valid) => {
          if (valid) {
            // alert('submit!')
            // this.disabled = true
            this.itemMsg = '<em>' + myTime + '</em>' + '<em>' + this.form.userName + '</em>' + '<em>' + this.form.foodType.label + '</em>' + '(' + this.form.count.label + ')'
          } else {
            console.warn('error submit!!')
            return false
          }
        })
        // 点餐请求部分
        const url = `getDinner/${this.form.userName}`
        Bus.get(url, (data) => {
          let userNameArr = data.result
          if (data.code === 1) {
            if (userNameArr.length !== 0 || typeof userNameArr === undefined) {
              this.$message({
                type: 'error',
                message: '已有人帮您点餐，请勿重复点！'
              })
            } else {
              this.addFuntion()
            }
          } else {
            this.$message({
              type: 'error',
              message: data.msg
            })
          }
        })
      },
      addFuntion () {
        let myDate = new Date()
        let myTime = utils.formatDate(myDate, 'datetime')
        if (this.btnShow === false) {
          const url = 'addDinner'
          const addData = {
            userName: this.form.userName,
            type: this.form.foodType.value,
            count: this.form.count.value,
            others: this.form.others
          }
          this.addDataStr = JSON.stringify(addData)// 转成JSON字符串
          Bus.storage().set('addDataStr', this.addDataStr)
          let viewData = {// 主食、份数页面显示用
            type: this.form.foodType.label,
            count: this.form.count.label,
            myTime: myTime
          }
          let viewDataStr = JSON.stringify(viewData)
          Bus.storage().set('viewDataStr', viewDataStr)
          Bus.post(url, addData, (data) => {
            if (data.code === 1) {
              this.$message({
                type: 'success',
                message: data.msg
              })
              // this.orderShow = true
              this.styleClass = 'background:#efa867'
              this.submitText = '已点餐'
              this.btnShow = true
              this.objectId = data.result.objectId
            } else {
              this.$message({
                type: 'error',
                message: data.msg
              })
            }
          })
        }
      },

      resetForm () { // 重置
        this.$refs.form.resetFields()
      },
      backout () { // 撤销
        this.resetForm()
        // this.disabled = false
        // 撤销
        const url = `cancelDinner/${this.objectId}`
        Bus.get(url, (data) => {
          if (data.code === 1) {
            this.$message({
              type: 'success',
              message: data.msg
            })
            // alert('撤销')
            this.itemMsg = ''
            this.styleClass = ''
            this.submitText = '我要点餐'
            this.btnShow = false
          } else {
            this.$message({
              type: 'error',
              message: data.msg
            })
          }
        })
      }
    }
  }
</script>

<style type="text/css">
/*    @import '/assets/css/order.css';*/
.el-form-item{display: inline-block;width: 100px;margin-right: 15px;margin-bottom: 15px;}
.el-textarea{width:343px;}
.slide-fade-enter-active {
  transition: all .5s ease;
}
.slide-fade-leave-active {
  transform: translateX(-10px);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active 在 <2.1.8 中 */ {
  transform: translateX(-100px);
  opacity: 0;
}
</style>



