// 日期格式化
/*
  传入参数列表
    d: date Object | number(毫秒/秒) | string(已经格式化的日期) | default: 当前时间
    type: 'datetime' | default: date
  demo:
    formatDate()  "2017-03-28"
    formatDate(new Date(), 'datetime') "2017-03-15 15:34"
*/
import Vue from 'vue'
import Resource from 'vue-resource'
Vue.use(Resource)

export const formatDate = function (d, type) {
  if (!d) {
    d = timeZone(new Date())
  } else if (typeof d === 'object') {
    d = timeZone(d)
  } else if (typeof d === 'number') {
    if (d.toString().length === 10) {
      d = d * 1000
    }
    d = timeZone(d)
  } else {
    if (typeof d === 'string') {
      if (d.length === 10 || d.length === 16) {
        return d
      } else if (d.length === 24) {
        d = new Date(d)
        d = timeZone(d)
        return d.toISOString().slice(0, 10)
      }
    }
    throw new Error('Unknow this Date type, in utils.js, function formatDate')
  }

  // 输出 datetime
  if (type === 'datetime') {
    return d.toISOString().substring(0, 16).replace('T', ' ')
  }

  // 输出 date
  return d.toISOString().slice(0, 10)
}

// 因为toISOString默认转为格林尼治时间，所以在转换之前需要额外加上8个小时
function timeZone (time) {
  if (typeof time === 'object') {
    time = time.getTime()
  }
  return new Date(time + 28800000)
}

// 解析JSON Object，并加入异常处理
/*
  传入参数列表
    str: 要解析的字符串
    def: 如果解析失败，所返回的默认值，不填则返回一个空数组
  demo:
    json('{key: value}', {})
*/

export const json = function (str, def) {
  try {
    return JSON.parse(str)
  } catch (e) {
    console.warn('捕获异常：', e, str)
    return def || []
  }
}

let utils = new Vue()
utils.formatDate = formatDate
export default utils



